.bnr-root {
    background-image: url('banner3.jpeg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.bnr-degraded {
    background-image: -webkit-gradient(linear, left bottom, left top, from(rgba(0,0,0,0)), to(rgba(0,0,0,1)));
    background-image: -webkit-linear-gradient(bottom, rgba(0,0,0,0), rgba(0,0,0,1));
    background-image: -o-linear-gradient(bottom, rgba(0,0,0,0), rgba(0,0,0,1));
    background-image: linear-gradient(to top, rgba(0,0,0,0), rgba(0,0,0,1));
}

.bnr-banner-dimention, .bnr-degraded {
    height: 300px;
}

.bnr-title {
    padding-top: 24px;
}

/* // Extra small devices (portrait phones, less than 576px) */
@media all and (max-width: 599px) {
    .bnr-banner-dimention, .bnr-degraded {
        height: 200px;
    }
}

/* // Small devices (landscape phones, 576px and up) */
@media all and (min-width: 600px) and (max-width: 959px) {}

/* // Medium devices (tablets, 768px and up) */
@media all and (min-width: 960px) and (max-width: 1279px) {}

/* // Large devices (desktops, 992px and up) */
@media all and (min-width: 1280px) and (max-width: 1919px) {}

/* // Extra large devices (large desktops, 1200px and up) */
@media all and (min-width: 1920px) {}


